import React, { createRef, lazy, PureComponent, Suspense  } from "react";
import { connect } from "react-redux";
import { Splide } from '@splidejs/react-splide';
import { Grid } from '@splidejs/splide-extension-grid';
import GalleryRow from "./GalleryRow";
import { isTagShop } from "../../../actions/api"
import '../scss/ThemesSplide.scss'
import './scss/GalleryTheme.scss'
import { nextData } from "../../../actions/moredataAction";

class GalleryTheme extends PureComponent {
  constructor(props) {
    super(props);
    this.state = { parent_id: '', loadData : false }
    this.inputRefs = {};
    this.sliderRef  = createRef();
    this.splide = null;
    this.isDragging = false; // Class property to track dragging
  }

  setInputRef = (inputName, el) => {
    if (el)  this.inputRefs[inputName] = el;
  };


  componentDidMount() {

    const slideCurrent =  this.sliderRef.current;
     if (slideCurrent) {
       // Detect user drag (touch move)
       slideCurrent.splide.on("drag", () =>{
          this.isDragging = true; // Reset flag after move
          !this.state.loadData && this.updatepost(0)
        } );
        // Listen for auto move (autoplay)
        slideCurrent.splide.on("move", () => { !this.state.loadData && !this.isDragging && this.updatepost(3)} );
    }

    setTimeout(() => {
      const classNameRole = document.querySelectorAll('.splide__slide');
      classNameRole && classNameRole.forEach((element) => {
        element.removeAttribute("aria-hidden");
        element.removeAttribute("role");
      });
    }, 300);

     this.props.loadBranding()
  }

   updatepost = (e) => {    
     if(!this.state.loadData){ 
       this.setState({ loadData : true })
       setTimeout(() => this.props.loadMoredata() , (e*1000))
     }
 }


  componentWillUnmount() {
    if (this.splide) {
      this.splide.destroy();
    }
  }

  
  requestData = () => {  
    const { postData, ThemeStyle, ThemeInfo, preRender, hasMoreData } = this.props;
    !this.state.loadData && this.updatepost(0) 
    if (hasMoreData) {
      nextData(
        isTagShop ? ThemeInfo.webId : ThemeInfo.wallId,
        Math.floor(Date.now() / 1000),
        ThemeStyle.totalPosts,
        postData?.appendData?.networkID,
        postData.appendData?.after,
        preRender,
        postData.appendData?.heightEvent
      );
    }
  };
  render() {
    const { SnapUp, postData, completeDataObject,  CardStyle, ThemeStyle, ProductSetting, ThemeInfo, clickToShowPopUp, parentID, onClickToCTA,  onClickPopUpSlider, postSizeInPx, handlePressEnd, handlePressStart, containerSize } = this.props;
    const desktopColumn = ThemeStyle.column.desktop;
    const mobileColumn = ThemeStyle.column.mobile;
    const slidePost = ThemeStyle.slide.autoSlide;
    const slideDuration = ThemeStyle.slide.slideDuration;
    const rowCount = ThemeStyle.row;
    const minCardSize = containerSize.width > 767 ? 600 : 500;
    const responsivePost = Math.max((containerSize.width / minCardSize).toFixed(), 1)
    const slideView =  containerSize.width > 767 ? desktopColumn ? desktopColumn : responsivePost : mobileColumn ? mobileColumn : responsivePost;

    const settings = {

      grid: {
        rows: rowCount,
      },
      rewind: true,
      type: 'slide', // or consider: rewind: true,
      speed: 1000,
      interval: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      perPage: slideView,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: 0,
      pagination: false,
      dragThreshold: 100,
      arrows: true,
      // breakpoints: {
      //   560: { perPage: mobileColumn || 1 },
      //   767: { perPage: mobileColumn || 2 },
      //   991: { perPage: mobileColumn || 3 },
      //   1200: { perPage: desktopColumn || (isTagShop ? 3 : 4) },
      //   1400: { perPage: desktopColumn || (isTagShop ? 3 : 5) },
      //   1600: { perPage: desktopColumn || (isTagShop ? 3 : 6) },
      // },
    };
    let newPostdata = [];

    const isOnsite = SnapUp.Form.status && SnapUp.ruleStatus ? true : false;
    for (let i = 0; i < postData.length;) {
      let gridLength = i == 0 && isOnsite ? 2 : 3;
      const merger = gridLength == 2 ? [0].concat(postData.slice(i, i + gridLength)) : postData.slice(i, i + gridLength)
      newPostdata.push(merger);
      i += gridLength;
    }

    newPostdata = postData.length < 9 ? Array(Math.floor((postData.length/2))).fill(newPostdata).flat() : newPostdata;

    return (postData && postData.length > 2) ? <div className="tb_gt_post_container">
      <Splide  hasTrack={false} className="tb_gt_post_slider" options={settings} extensions={{ Grid }} ref={this.sliderRef}>
        <div className="splide__track">
          <div className="splide__list">
              {newPostdata.map((item, index) => {
                return (item && item.length == 3) ? <div key={index} id={`tb_gt-${index}`} className={`splide__slide tb_${index}`} idd={parentID}>
                    <GalleryRow  parentID={parentID} handlePressStart={handlePressStart} handlePressEnd={handlePressEnd} completeDataObject={completeDataObject} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} Post={postData} index={index} SnapUp={SnapUp} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ProductSetting={ProductSetting} ThemeInfo={ThemeInfo} itemData={item} isOnsite={isOnsite}  />
                </div> : null
              })}
          </div>
        </div>
        <div className="splide__arrows splide__arrows--ltr tb_gt_arrow_wrapper_">
          <div className="tb_gt_arrow splide__arrow splide__arrow--prev tb_gt_arrow tb_gt_arrow_left__ tb__icon tb-arrow-left-alt" role="button" aria-label="Go to previous slide"><div></div></div>
          <div className="tb_gt_arrow splide__arrow splide__arrow--next  tb_gt_arrow_right__ tb__icon tb-arrow-right-alt" onClick={this.requestData} role="button" aria-label="Go to next slide"><div></div></div>
        </div>
      </Splide>
    </div> : null
  }
}

const mapDispatchToProps = dispatch => {
  return {
    nextData: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect("", mapDispatchToProps)(GalleryTheme);