import { SplideSlide } from "@splidejs/react-splide";
import React, { lazy, Suspense } from "react";
const Card = lazy(() => import('./Elements/Card'));
const CardSnapUp = lazy(() => import('../Elements/CardSnapUp'));


export const CardPost = ({ CardStyle,handlePressEnd, handlePressStart,  ThemeStyle, itemData, completeDataObject, index, clickToShowPopUp, postSizeInPx, onClickPopUpSlider, dataIndex }) => {
    return <SplideSlide   
            onTouchStart={handlePressStart}
            onTouchEnd={(e) => handlePressEnd(e, index, itemData[dataIndex])}
            >
          <Suspense fallback={<></>}><Card  CardStyle={CardStyle} ThemeStyle={ThemeStyle} Post={completeDataObject[itemData[dataIndex]]} index={index} clickToShowPopUp={clickToShowPopUp} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} /></Suspense></SplideSlide>
}
const Rows = (props) => {
    return <div className="tb_gt_post_row">
        <div className="tb_gt_post_sm">
            {props.isOnsite && props.index == 0 ?
                <div className={`tb_gt_post_wrapper tb_onsite_post__`}>
                    <Suspense fallback={<></>}><CardSnapUp className="tb_gt_post_in" {...props} wallId={props.ThemeInfo.wallId} webId={props.ThemeInfo.webId} /></Suspense>
                </div>
                : <CardPost {...props} dataIndex={0} />
            }
            <CardPost {...props} dataIndex={1} />
        </div>
        <div className="tb_gt_post_lg">
            <CardPost {...props} dataIndex={2} />
        </div>
    </div>
}

export default Rows